<template>
  <Table
    border
    ref="selection"
    :loading="loading"
    :columns="tableColumns"
    :data="dataSourceWithKey"
    @on-selection-change="handleSelectionChange"
  >
    <template slot-scope="{ row }" slot="tokenId">
      <div class="flex-center">
        <span>{{ displayOptimize(row.tokenId) }}</span>
        <Button
          type="primary"
          size="small"
          :id="`btn-${row.key}`"
          :data-clipboard-text="row.tokenId"
          style="margin-left: 10px"
          @click="copyAddr(row.key)"
        >复制</Button>
      </div>
    </template>
  </Table>
</template>

<script>
import { Table, Button } from "view-design";
import ClipboardJs from "clipboard";
import { genUniqueKey } from '../utils/index';

export default {
  components: {
    Table,
    Button,
  },
  props: {
    dataSource: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Array,
      required: true,
    },
  },
  computed: {
    tableColumns() {
      return [
        {
          type: "selection",
          width: 60,
          align: "center",
        },
        {
          title: "token id",
          width: 200,
          slot: "tokenId",
        },
        {
          title: "职业",
          key: "career",
        },
        {
          title: "力量",
          key: "strength",
        },
        {
          title: "敏捷",
          key: "agility",
        },
        {
          title: "体质",
          key: "physique",
        },
        {
          title: "意志",
          key: "volition",
        },
        {
          title: "智力",
          key: "brains",
        },
        {
          title: "精神",
          key: "charm",
        },
        {
          title: "收益",
          key: "gold",
        },
      ];
    },
    dataSourceWithKey() {
      return this.dataSource.map(d => ({ ...d, key: genUniqueKey() }))
    },
  },
  methods: {
    displayOptimize(careerAddr) {
      const res = /(\w{4}).+(\w{4})/.exec(careerAddr);
      return `${res[1]}....${res[2]}`;
    },
    copyAddr(rowKey) {
      const clipboard = new ClipboardJs(`#btn-${rowKey}`)
      clipboard.on('success', () => {
        this.$message.success("复制成功");
        clipboard.destroy()
      })
    },
    handleSelectionChange(selections = []) {
      this.$emit('input', selections.map(s => s))
    }
  },
};
</script>

<style scoped>
.flex-center {
  display: flex;
  align-items: center;
}
</style>
